import { RegReportadminComponent } from './reg-reportadmin/reg-reportadmin.component';
import { SitReportComponent } from './sit-report/sit-report.component';
import { PostingsComponent } from './postings/postings.component';
import { UtmeCentresComponent } from './utme-centres/utme-centres.component';
import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { RegReportComponent } from './reg-report/reg-report.component';
import { EyewitnessComponent } from './eyewitness/eyewitness.component';
import { EyewitnessadminComponent } from './eyewitnessadmin/eyewitnessadmin.component';


const ExaminationRoutes: Routes = [
   { path: 'utmecentres', component:  UtmeCentresComponent , data: { animation: 'buttons' }},
    { path: 'postings', component:  PostingsComponent , data: { animation: 'buttons' }},
    {path: 'sitreport', component: SitReportComponent, data: { animation: 'buttons' }},
    {path: 'regreport', component: RegReportComponent, data: { animation: 'buttons' }},
    {path: 'regreportadmin', component: RegReportadminComponent, data: { animation: 'buttons' }},
    {path: 'eyewitness', component: EyewitnessComponent, data: {animation: 'buttons'}},
    {path: 'eyewitadm', component: EyewitnessadminComponent, data: {animation: 'buttons'}},
    {path: '', redirectTo: '/auth/dashboard', pathMatch: 'full'}
];

@NgModule({
    imports: [
        RouterModule.forChild(ExaminationRoutes)
    ],
    exports: [
        RouterModule
    ]
})

export class ExaminationRouterModule {}
