import { ViewdialogComponent } from './../dialog/viewdialog/viewdialog.component';
import { ExaminationService } from './../examinations.service';
import { SituationReport, State, UtmeCentres, Statess, RegistrationReport } from './../examinations.model';
import { Component, OnInit, OnDestroy, ViewChild, AfterViewInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators, NgForm } from '@angular/forms';
import { ToastrService } from 'ngx-toastr';
import { MatTableDataSource, MatPaginator, MatSort, MatDialog } from '@angular/material';
 import {DataSource} from '@angular/cdk/collections';
import { Session } from 'protractor';


@Component({
  selector: 'app-reg-report',
  templateUrl: './reg-report.component.html',
  styleUrls: ['./reg-report.component.css']
})
export class RegReportComponent implements OnInit, AfterViewInit, OnDestroy  {

  registrationreport: RegistrationReport[];
  state: State[];
  selectedstate: Statess = new Statess (1, 'ABIA');
  centres: UtmeCentres[];
  StoredCentres: UtmeCentres[];
  DisplayedCentres: UtmeCentres[];
  loggedby: string;

 /*  displayedColumns = ['CentreID', 'CentreName', 'Session', 'Caller', 'IssueCategory', 'IssueStatus', 'LoggedBy', 'LoggedDate']; */
  displayedColumns = ['SerialNo', 'StateName', 'CentreName', 'IssueCategory', 'IssueStatus', 'LoggedDate', 'actions'];


  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;
  LogFormgroup: FormGroup;
  dataSource: MatTableDataSource<RegistrationReport>;

  constructor(private _formBuilder: FormBuilder, private toastr: ToastrService,
    private examinationservice: ExaminationService,
    private dialog: MatDialog) { }

  ngOnInit() {
    this.loggedby = localStorage.getItem('profilename');
    this.LoadState();
    this.LoadCentre();
    this.LoadRegistrationReport();

    this.LogFormgroup = this._formBuilder.group({
      LogState: ['', Validators.required],
      LogCentre: ['', Validators.required],
      LogIssueStatus: ['', Validators.required],
      LogIssueCategory: ['', Validators.required],
      LogProblem: ['', Validators.required],
      LogSolution: ['', Validators.required],
    });
  }

  LoadState() {
    this.examinationservice.GetUTMEState().subscribe(
      result => {this.state = result; },
      error => console.error(error));
  }

  LoadCentre() {
    this.examinationservice.GetRegistrationCentres().subscribe(
      result => {this.StoredCentres = result; },
      error => console.error(error));
  }

  LoadRegistrationReport() {
    this.examinationservice.GetUTMERegistration(this.loggedby).subscribe(
      result => {
        this.dataSource = new MatTableDataSource(result);
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort; },
      error => console.error(error));
  }

  applyFilter(filterValue: string) {
    filterValue = filterValue.trim(); // Remove whitespace
    filterValue = filterValue.toLowerCase(); // Datasource defaults to lowercase matches
    this.dataSource.filter = filterValue;
  }

  StartView(i: number, serialno: string, centrename: string,  issuecategory: string,
    issuestatus: string, problemdesc: string, solution: string, loggedby: string, loggeddate: string) {
    const dialogref = this.dialog.open(ViewdialogComponent, {
    data: {i: i, serialno: serialno, centrename: centrename,
             issuecategory: issuecategory, problemdesc: problemdesc, solution: solution, issuestatus: issuestatus, loggedby: loggedby,
        loggeddate: loggeddate}
    });
    dialogref.afterClosed().subscribe(() => {
    this.LoadRegistrationReport();
    });

  }
  LoadCentres(stat) {
    this.DisplayedCentres = this.StoredCentres.filter((item) => item.StateName === stat);
  }


  SubmitLog() {
    this.examinationservice.InsertRegistrationReport(this.LogFormgroup.value, this.loggedby).subscribe(result => {
    this.showSuccess();
    this.LoadRegistrationReport();
    this.LogFormgroup.reset();
    },
    error => {
      this.showError();
    });

  }

  showSuccess() {
    this.toastr.success('Updated Succesfully');
  }
  showError() {
    this.toastr.error('Update Failed, Please try again');
  }

  ngAfterViewInit() {

    }

    ngOnDestroy() {

    }

}
