import { LoaderService } from './../../loader/loader.service';
import { ExaminationService } from './../examinations.service';
import { Component, OnInit, OnDestroy, ViewChild, AfterViewInit } from '@angular/core';
import { map } from 'rxjs/operators';
import { Observable, Subscription } from 'rxjs';
import { UtmeCentres } from '../examinations.model';
import { Router } from '@angular/router';
import { MatTableDataSource, MatPaginator, MatSort } from '@angular/material';
 import {DataSource} from '@angular/cdk/collections';

@Component({
  selector: 'app-utme-centres',
  templateUrl: './utme-centres.component.html',
  styleUrls: ['./utme-centres.component.scss']
})
export class UtmeCentresComponent implements OnInit, AfterViewInit, OnDestroy {
  utmecentres: UtmeCentres[];
  UtmeCentreSubscription: Subscription;
  displayedColumns = ['statename', 'Examtown', 'CentreName'];
   dataSource: MatTableDataSource<UtmeCentres>;
   // datasource = new UtmeCentreDataSource(this.examinationservice);

  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;

  constructor(private examinationservice: ExaminationService,
    private loaderservice: LoaderService,
    private router: Router) { }

  ngOnInit() {
    this.loaderservice.show();
    this.examinationservice.GetUTMECentres().subscribe
    (result => {
      this.dataSource = new MatTableDataSource(result);
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;
      this.loaderservice.hide();
    }
    , error => console.error(error));
  }



  applyFilter(filterValue: string) {
    filterValue = filterValue.trim(); // Remove whitespace
    filterValue = filterValue.toLowerCase(); // Datasource defaults to lowercase matches
    this.dataSource.filter = filterValue;
  }

  ngAfterViewInit() {
  //  this.dataSource.paginator = this.paginator;
// this.dataSource.sort = this.sort;
  }

  ngOnDestroy() {

  }

}


