import { UtmeCentres, Statess, State } from './../examinations.model';
import { Globals } from './../../Globals';
import { Component, OnInit, ÉµConsole } from '@angular/core';
import { FormBuilder, FormGroup, Validators, NgForm } from '@angular/forms';
import { FileSelectDirective, FileUploader} from 'ng2-file-upload';
import {saveAs} from 'file-saver';
import { UploadsService } from '../../uploads/uploads.service';
import { ToastrService } from 'ngx-toastr';
import { ExaminationService } from '../examinations.service';




@Component({
  selector: 'app-eyewitness',
  templateUrl: './eyewitness.component.html',
  styleUrls: ['./eyewitness.component.css']
})
export class EyewitnessComponent implements OnInit {
  state: State[];
  selectedstate: Statess = new Statess (1, 'ABIA');
  centres: UtmeCentres[];
  StoredCentres: UtmeCentres[];
  DisplayedCentres: UtmeCentres[];
  loggedby: string;
  LogFormgroup: FormGroup;
  filename: any;

  readonly uri = 'http://' + this.globals.ipaddress + ':3000/uploads/uploadEyewitness';

  uploader = new FileUploader({url: this.uri});

    attachmentList: any = [];


  constructor(private _fileService: UploadsService,
    private globals: Globals, private _formBuilder: FormBuilder, private toastr: ToastrService,
    private examinationservice: ExaminationService) {

     this.uploader.onCompleteItem = (item: any, response: any , status: any, headers: any) => {
      this.attachmentList.push(JSON.parse(response));
      this.filename = item.file.name;
      this.SubmitLog();
  };

  }

ngOnInit() {
  this.loggedby = localStorage.getItem('profilename');
  this.LoadState();
  this.LoadCentre();


  this.LogFormgroup = this._formBuilder.group({
    LogState: ['', Validators.required],
    LogCentre: ['', Validators.required],
    LogProblem: ['', Validators.required],
  });
}

SubmitLog() {
  this.examinationservice.InsertEyeWitnessReport(this.LogFormgroup.value, this.loggedby, this.filename).subscribe(result => {
    this.showSuccess();
    },
    error => {
      this.showError();
    });

}

showSuccess() {
  this.toastr.success('Uploaded Succesfully');
}
showError() {
  this.toastr.error('Update Failed, Please try again');
}

LoadState() {
  this.examinationservice.GetUTMEState().subscribe(
    result => {this.state = result; },
    error => console.error(error));
}

LoadCentre() {
  this.examinationservice.GetRegistrationCentres().subscribe(
    result => {this.StoredCentres = result; },
    error => console.error(error));
}

  LoadCentres(stat) {
    this.DisplayedCentres = this.StoredCentres.filter((item) => item.StateName === stat);
  }


}
