import { UtmeCentresComponent } from './utme-centres/utme-centres.component';
import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { ReactiveFormsModule, FormsModule } from '@angular/forms';
import { FlexLayoutModule } from '@angular/flex-layout';
import { MatButtonModule } from '@angular/material/button';
import { MatIconModule } from '@angular/material/icon';
import { MatTabsModule } from '@angular/material/tabs';
import { MatToolbarModule } from '@angular/material/toolbar';
import { MatListModule } from '@angular/material/list';
import { MatStepperModule } from '@angular/material/stepper';
import { MatInputModule } from '@angular/material/input';
import { MatFormFieldModule } from '@angular/material/form-field';
import { MatExpansionModule } from '@angular/material/expansion';
import { MatDatepickerModule } from '@angular/material/datepicker';
import { MatNativeDateModule } from '@angular/material';
import {MatSortModule} from '@angular/material/sort';
import { MatProgressSpinnerModule,
         MatRadioModule,
         MatSliderModule,
         MatPaginatorModule,
         MatCardModule,
         MatTableModule,
         MatAutocompleteModule,
         MatCheckboxModule,
         MatSlideToggleModule,
         MatSelectModule,
         MatProgressBarModule,
         MatMenuModule,

 } from '@angular/material';
 import * as hljs from 'highlight.js';
import { HighlightJsModule, HIGHLIGHT_JS } from 'angular-highlight-js';
import * as hljsTypescript from 'highlight.js/lib/languages/typescript';

 import { ExaminationRouterModule } from './examinations.router';
import { PostingsComponent } from './postings/postings.component';
import { SitReportComponent } from './sit-report/sit-report.component';
import { ViewdialogComponent } from './dialog/viewdialog/viewdialog.component';
import { RegReportComponent } from './reg-report/reg-report.component';
import { RegReportadminComponent } from './reg-reportadmin/reg-reportadmin.component';
import { EyewitnessComponent } from './eyewitness/eyewitness.component';
 export function highlightJsFactory(): any {
    hljs.registerLanguage('typescript', hljsTypescript);
    return hljs;
  }
  import {FileUploadModule} from 'ng2-file-upload';
import { EyewitnessadminComponent } from './eyewitnessadmin/eyewitnessadmin.component';



@NgModule({
    imports: [
    CommonModule,
    ExaminationRouterModule,
    FlexLayoutModule,
    FileUploadModule,
    MatAutocompleteModule,
    MatButtonModule,
    MatCardModule,
    MatCheckboxModule,
    MatPaginatorModule,
    MatProgressSpinnerModule,
    MatIconModule,
    MatTabsModule,
    MatRadioModule,
    MatToolbarModule,
    MatListModule,
    MatStepperModule,
    MatSortModule,
    ReactiveFormsModule,
    FormsModule,
    MatFormFieldModule,
    MatInputModule,
    MatTableModule,
    MatMenuModule,
    MatExpansionModule,
    MatDatepickerModule,
    MatNativeDateModule,
    MatSliderModule,
    MatSelectModule,
    MatSlideToggleModule,
    MatProgressBarModule,
    HighlightJsModule.forRoot({
      provide: HIGHLIGHT_JS,
      useFactory: highlightJsFactory
    }),
    ],

    declarations: [
        UtmeCentresComponent,
        PostingsComponent,
        SitReportComponent,
        ViewdialogComponent,
        RegReportComponent,
        RegReportadminComponent,
        EyewitnessComponent,
        EyewitnessadminComponent,
    ],
    entryComponents: [
      ViewdialogComponent
    ],
    exports: [
      FileUploadModule
    ]
})
export class ExaminationModule { }
