import { PostingDetails, OtherPostingDetails } from './../examinations.model';
import { ExaminationService } from './../examinations.service';
import { Component, OnInit } from '@angular/core';
import { ToastrService } from 'ngx-toastr';


@Component({
  selector: 'app-postings',
  templateUrl: './postings.component.html',
  styleUrls: ['./postings.component.scss']
})

export class PostingsComponent implements OnInit {
  idcard: any;
  postingdetails: PostingDetails[];
  otherpostingdetails: OtherPostingDetails[];
  twncode: any;

  constructor(private examinationservice: ExaminationService,  private toastr: ToastrService) { }

  ngOnInit() {
    this.idcard = localStorage.getItem('idcard');
    this.LoadPostingDetails();
  }

  showSuccess() {
    this.toastr.success('');
  }

  showError() {
    this.toastr.error('Sorry, You have not been posted');
  }


  LoadPostingDetails() {
    this.examinationservice.GetPostings(this.idcard).subscribe
    (result => { this.postingdetails = result;
      if (result != null) {
        this.LoadOtherPostingDetails(this.postingdetails[0].TwnCode);
      } else {
        this.showError();
      }
    }, error => {
      this.showError();
    });
  }


  LoadOtherPostingDetails(twncod: any) {
    this.examinationservice.GetPostingsOthers(this.idcard, twncod).subscribe
    (result => { this.otherpostingdetails = result;
    }, error => {
      this.showError();
    });

  }
}
